import React from 'react';
import './NavBar.css';
// import Dropdown from 'react-bootstrap/Dropdown';
import DropdownButton from 'react-bootstrap/DropdownButton';
import { Container } from 'react-bootstrap';
import { Link } from "react-router-dom";

const NavBar = () => {
    const manuCloseHandler=()=>{
        const cross = document.getElementById('manu-items');
        cross.style.left = "-100%"
    }
    const manuOpenHandler=()=>{
        const cross = document.getElementById('manu-items');
        cross.style.left = "0"
        cross.style.width = "60%"
    }
    return (
        <nav className='nav'>
        <input type="checkbox" id="sort-menu"/>
        <label htmlFor="sort-menu">
            <i className="fas fa-bars appbar" id="menu-yes" onClick={()=>manuOpenHandler()}></i>
            <i className="fas fa-times crossbar" id="menu-no"></i>
        </label>
        <Container>
      <ul id='manu-items'>
        <li> 
            <div className="div">
                <Link to="/" id="Home">Home</Link>
                <span className="menu-no" onClick={()=>manuCloseHandler()}> <i className="fas fa-times manu_icons_mobile" id='cross'></i></span>
            </div>
        </li>
        <li>
         <DropdownButton id="dropdown-basic-button" title="Bras">
            <Link className='drop-btn' to="/braSet">Bra Set</Link>
            <Link className='drop-btn' to="/padded">Padded Bra</Link>
            <Link className='drop-btn' to="/NonePadded">Non Padded Bra</Link>
            <Link className='drop-btn' to="/wireless">Wireless Bra</Link>
            <Link className='drop-btn' to="/sports">Sports Bra</Link>
            <Link className='drop-btn' to="/bodySuit">BodySuit/S.Suit</Link>
            <Link className='drop-btn' to="/bikini">Bikini</Link>
            <Link className='drop-btn' to="/teenager">Teenager Bra</Link>
            <Link className='drop-btn' to="/motherBra">Mother Bra</Link>
            <Link className='drop-btn' to="/lingerie">Lingerie</Link>
        </DropdownButton>
        </li>
        <li><Link to="/shop">Shop</Link></li>
        <li><Link to="/nightWear">Night-wear</Link></li>
        <li><Link to="/panty">Panties</Link></li>
        <li><Link to="/tshirt">T-Shirt</Link></li>
        <li>
        <DropdownButton id="dropdown-basic-button" title="Other Product">
            <Link className='drop-btn' to="/menstrual">Menstrual Cup</Link>
            <Link className='drop-btn' to="/napkin">Sanitary Napkin</Link>
            <Link className='drop-btn' to="/skirts">Skirts</Link>
            <Link className='drop-btn' to="/shorts">Shorts</Link>
            
        </DropdownButton>
        </li>
        <li><Link to="/Blog">Blogs</Link></li>
        <li><Link to="/SizeCalculator">Size-Calculator</Link></li>
        <li><Link to="/">Login</Link></li>
      </ul>
      </Container>
      </nav>
    );
};

export default NavBar;